@use '../../css/mixins.scss' as m;
.skills {
    background-color: #fff;
}

.skills-wrapper {
    display: grid;
    grid-gap: 0.2em;
    row-gap: 2em;
    grid-template-columns: repeat(2, 0.5fr);
    @include m.breakpoint(tabletUp) {
        grid-template-columns: repeat(4, 0.5fr);
    }
    align-content: center;
    justify-content: center;

    padding-top: 2em;
    padding-bottom: 4em;
}

.skill-container {
    color: #000;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;

    img {
        width: 4.5em;
        height: auto;
        max-height: 250px;
        // border-radius: 50%;
        // border: 2px solid #000;
    }

    p {
        padding: 0;
        margin: 0.6em 0 0 0;
        text-align: center;
        white-space: pre-wrap;
    }
}
