.bio__wrapper{display:grid;grid-template-columns:auto;grid-template-areas:"title" "headline" "content1" "content2";justify-content:center;align-items:center;padding-top:2em;padding-bottom:4em;row-gap:2em;background-color:#fff}@media(min-width: 768px){.bio__wrapper{grid-template-columns:1fr 8fr 1fr;grid-template-areas:"title title title" ". headline ." ". content1 ." ". content2 .";justify-content:start;align-items:start}}.bio__wrapper .title{grid-area:title;align-self:start;justify-self:unset}.bio__wrapper .bio-text{color:#000;font-weight:400;font-size:1rem}.bio__wrapper .header{quotes:"“" "”" "‘" "’";padding:1em 3em;grid-area:headline;text-align:center}.bio__wrapper .header h2,.bio__wrapper .header h3{font-family:"Commissioner",sans-serif;color:#1c171d;font-size:1.2rem}@keyframes fontAddWeight{from{font-weight:200}to{font-weight:800}}.bio__wrapper .header h2{font-size:2.5rem;font-weight:500;margin:0 0}.bio__wrapper .header h2 .bolder{font-weight:600;transition:font-weight 6s linear;animation-delay:1s;animation-fill-mode:forwards}.bio__wrapper .header h3{font-size:2rem;font-weight:400;margin:.3em 0 1em 0}.bio__wrapper .para1{grid-area:content1;padding:0 0;margin:0 1em}.bio__wrapper .para1 h3{font-weight:450;margin:0 1em}.bio__wrapper .para1 p{margin:1em 2em 1em;font-size:1rem}.bio__wrapper .para2{grid-area:content2;margin:0 1em;padding:0 1em}.bio__wrapper .para2 h3{font-weight:450}.bio__wrapper .para2 p{margin:0 2em;line-height:1.6rem}.bio__wrapper .para2 ul{margin:1em 2em}.bio__wrapper .para2 li{list-style:none;display:flex;font-size:1.1rem;line-height:1.7rem;align-items:flex-start;justify-content:flex-start;gap:10px;margin-bottom:.5em}